import { calculateNiceNum } from './nicenum';
import { selectionToGroup } from './selectionToGroup';
import { canvasCreate, processData, fetchData } from './common';

export default function (context) {
	const fetch = require('sketch-polyfill-fetch');
	const url = context.plugin.urlForResourceNamed("params.json");
	let result = NSString.stringWithContentsOfFile_encoding_error(url, NSUTF8StringEncoding, null);
	let params = JSON.parse(result);
	const canvas = canvasCreate(context);
	canvas.doc.currentPage().changeSelectionBySelectingLayers_([]);

	function createScatterPlot() {
		let maxDCount = 0,
			N = 2;

		if (params.scatterParams.bubbles === "true") {
			let maxDiametr = [],
				categories = dataObj.table.length / 3,
				maxDTemp = [];

			N = 3;

			for (let i = 0; i < dataObj.rows / 3; i++){
				for (let j = 0; j < dataObj.columns; j++){
					maxDTemp[j] = Number(dataObj.table[i * categories + 2][j]);
				}
				maxDiametr[i] = Math.max.apply(null, maxDTemp);
			}
			maxDCount = Math.max.apply(null, maxDiametr);
		}

		for (let i = 0; i < dataObj.rows / N; i++){

			for (let j = 0; j < dataObj.columns; j++){

				let maxValue = dataObj.niceMax,
					minValue = 0,
					categories = dataObj.table.length / 2,
					diameter = params.scatterParams.diametr;
				if (params.scatterParams.bubbles === "true") {
					categories = dataObj.table.length / 3;
				}

				log(categories);

				if (params.scatterParams.bubbles === "true") {
					diameter = (canvas.width * 0.1) / maxDCount * Number(dataObj.table[i * categories + 2][j]);
				}
				
				let pointX = canvas.x + (canvas.width / maxValue) * Number(dataObj.table[i * categories][j]),
					pointY = canvas.y + canvas.height - (canvas.height / maxValue) * Number(dataObj.table[i * categories + 1][j]);

				let scatterCircle = NSBezierPath.bezierPath();
				scatterCircle.appendBezierPathWithArcWithCenter_radius_startAngle_endAngle(NSMakePoint(pointX,pointY), diameter/2, 0, 360);
				let newBezier = MSPath.pathWithBezierPath(scatterCircle);
				let scatterShape = MSShapeGroup.shapeWithBezierPath(newBezier);
				let fill = scatterShape.style().addStylePartOfType(0);
				fill.color = MSColor.colorWithRed_green_blue_alpha(params.colorPalete[i][0]/255,params.colorPalete[i][1]/255,params.colorPalete[i][2]/255,0.8);

				scatterShape.setName("c_scatterPoint_" + ( i + 1 ) + "_" + ( j + 1 ));
				let scatterArray = NSArray.arrayWithArray([scatterShape]);

				if (canvas.doc.currentPage().currentArtboard() === null){
					canvas.doc.currentPage().addLayers(scatterArray);
				} else{
					canvas.doc.currentPage().currentArtboard().addLayers(scatterArray);
				}

				scatterShape.select_byExpandingSelection(true, true);

			}

		};
		canvas.layer.select_byExpandingSelection(true, true);

		selectionToGroup(canvas, "Scatter plot");

	}

	function drawChart(){
		let niceScales = calculateNiceNum( dataObj.min, dataObj.max );
		dataObj.niceMax = niceScales.niceMaximum;
		dataObj.niceMin = niceScales.niceMinimum;

		if (canvas.layer.isKindOfClass(MSShapeGroup)) {
			createScatterPlot();
		} else {
			let plot = canvas.layer.firstLayer();
			canvas.height = plot.frame().height();
			canvas.width = plot.frame().width();
			canvas.x = plot.frame().x() + canvas.layer.frame().x();
			canvas.y = plot.frame().y() + canvas.layer.frame().y();
			
            plot.duplicate();
            let layersInGroup = canvas.layer.containedLayersCount();
            canvas.layer.layerAtIndex(0).select_byExpandingSelection(true, true);

            for (var i = 0; i < layersInGroup - 1; i++) {
				var layer = canvas.layer.layerAtIndex(1);
				layer.removeFromParent();
			}

			canvas.layer.ungroup();

			createScatterPlot();
		}
	}

	let dataObjArray = processData();
	let dataObj = 0;
	if (dataObjArray[1] === false) {
		dataObj = dataObjArray[0];
		drawChart();
	} else {
		fetchData(dataObjArray[0]).then(response => {
			dataObj = response;
			drawChart();
		})
	}	
}